import React from 'react';
import SearchBar from './components/SearchBar';
import WeatherDisplay from './components/WeatherDisplay';
import weatherApi from './api/weatherApi';
import './styles/app.css';

class App extends React.Component {
	state = { weather: {}, loaded: false };
	onSearchSubmit = async (term) => {
		this.setState({ ...{ loaded: false } });
		try {
			const response = await weatherApi
				.get('/', {
					params: {
						q: term,
						units: 'metric',
						appid: '122596d426a243e17c8d4b72f457f4b7'
					}
				})
				.then((response) => {
					this.setState({ weather: response.data });
				});

			this.setState({ ...{ loaded: true } });
		} catch (e) {
			console.log(e);
			alert('Location not Valid');
			this.setState({ ...{ loaded: true } });
		}
	};

	render() {
		// console.log('this is -> ', this.state.weather.main);
		return (
			<div id="App">
				<header>Your weather?</header>
				<SearchBar onSubmit={this.onSearchSubmit} />

				<WeatherDisplay weatherData={this.state.weather} loaded={this.state.loaded} />
			</div>
		);
	}
	componentDidMount() {
		this.onSearchSubmit('Delhi');
	}
}

export default App;
